import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';

import './App.css';
import Portfolio from './modules/portfolio';
import Create from './modules/portfolio/pages/Create';
import Ticker from './modules/portfolio/pages/Ticker';
import View from './modules/portfolio/pages/View';
import Ticker2 from './modules/portfolio/pages/Ticker2';
import ViewPortfolio from './modules/portfolio/pages/ViewPortfolio';

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path='/portfolio' component={Portfolio} />
        <Route exact path='/create' component={Create} />
        <Route exact path='/ticker' component={Ticker} />
        <Route exact path='/view' component={View} />
        <Route exact path='/ticker2' component={Ticker2} />
        <Route exact={'/view-portfolio'} component={ViewPortfolio} />
        <Redirect to='/portfolio' />
      </Switch>
    </Router>
  );
}

export default App;
