import { Line } from "react-chartjs-2"
import React from "react"

const buildData = ({ chartData }) => ({
  type: 'line',
  labels: chartData.labels,
  datasets: [
    {
      label: "",
      borderWidth: 1,
      borderColor: "rgba(31, 208, 163, 1)",
      backgroundColor: function (context) {

        const chart = context.chart;
        const {ctx, chartArea} = chart;
        if (!chartArea) {
          return null;
        }
        let grad = ctx.createLinearGradient(50, 90, 50, 0);
        grad.addColorStop(0.0489, 'rgba(31, 208, 163, 0)');
        grad.addColorStop(1, 'rgba(31, 208, 163, 1)');

        return grad;

      },
      data: chartData.data,
      tension: 0.2,
      showLine: true,
      fill: true
    },
  ],
})

const options = {
  legend: {
    position: 'bottom',
  },
  elements: {
    point: {
      radius: 0,
      tension: 1
    },
  },
  scales: {
    x: {
      display: false
    },
    y: {
      display: false
    },
  },
  plugins: {
    legend: {
      display: false
    }
  },
}


const PortFolioChart = ({ info }) => {
  const data = buildData(info)

  return (
    <>
      <div className="w-24">
        <Line data={data} options={options} />
      </div>
    </>
  )
}

export default PortFolioChart
